import React, { Component } from 'react';
import { BrowserRouter as Router } from "react-router-dom";
import {
    Navbar,
    NavbarBrand,
    NavbarNav,
    NavbarToggler,
    Collapse,
    NavItem,
    Footer,
    NavLink,
  } from "mdbreact";

  import Aux from '../../hoc/aux';
  import Routes from '../../containers/Routes';
  
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
          collapseID: "",
          loggedin: false,
          userData: JSON.parse(window.sessionStorage.getItem('userData')),
        };
      }
      
      componentDidMount(){
        if(this.state.userData){
          this.setState({loggedin: true})
        }
      }
      
      toggleCollapse = collapseID => () =>
        this.setState(prevState => ({
          collapseID: prevState.collapseID !== collapseID ? collapseID : ""
        }));
    
      closeCollapse = collapseID => () =>
        this.state.collapseID === collapseID && this.setState({ collapseID: "" });
    
      
        //Search Handler for searchin of products
      
      render() {
        const overlay = (
          <div
            id="sidenav-overlay"
            style={{ backgroundColor: "transparent" }}
            onClick={this.toggleCollapse("mainNavbarCollapse")}
          />
        );
        return (
          <Router>
            <div className="flyout">
              <Navbar color="green" dark expand="md" fixed="top" scrolling>
                <NavbarBrand href="/">
                  <img
                    src="data:image/svg+xml;base64,"
                    alt=""
                    height="20"
                  />{" "}
                  DixCova
                </NavbarBrand>
                <NavbarToggler
                  onClick={this.toggleCollapse("mainNavbarCollapse")}
                />
                <Collapse
                  id="mainNavbarCollapse"
                  isOpen={this.state.collapseID}
                  navbar
                >
                  {this.state.userData ?
                    <Aux>
                      <NavbarNav right>
                    <NavItem>
                      <NavLink
                        exact
                        to="/"
                        onClick={this.closeCollapse("mainNavbarCollapse")}
                      >
                        Dashboard
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink
                        onClick={this.closeCollapse("mainNavbarCollapse")}
                        to=""
                      >
                        {this.state.userData.email}
                      </NavLink>
                    </NavItem>

                     
                    <NavItem>
                    <NavLink
                      onClick={this.closeCollapse("mainNavbarCollapse")}
                      to="/logout"
                    >
                      Logout
                    </NavLink>
                  </NavItem>
                    
                  </NavbarNav>
                    </Aux>
                  :<NavbarNav right>
                  <NavItem>
                    <NavLink
                      exact
                      to="/"
                      onClick={this.closeCollapse("mainNavbarCollapse")}
                    >
                      Home
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      onClick={this.closeCollapse("mainNavbarCollapse")}
                      to="/About"
                    >
                      About
                    </NavLink>
                  </NavItem>
                
                    <NavItem>
                    <NavLink
                      onClick={this.closeCollapse("mainNavbarCollapse")}
                      to="/login"
                    >
                      Login
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      onClick={this.closeCollapse("mainNavbarCollapse")}
                      to="/register"
                    >
                      Register
                    </NavLink>
                  </NavItem>
                  
                </NavbarNav>
              }
                </Collapse>
              </Navbar>

              {/* Footer */}
          {this.state.collapseID && overlay}
          <main style={{ marginTop: "0rem" }}>
            <Routes userData={this.state.userData}/>
          </main>

          <Footer color="green">
            <p className="footer-copyright mb-0 py-3 text-center">
              &copy; {new Date().getFullYear()} Copyright:{" "}
              <a href="https://www.dixcova.org"> DixCova.org </a>
            </p>
          </Footer>
        </div>
      </Router>
      );
    }
}

export default Navigation;


